@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,300i,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:400,600,600i&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
     "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
    box-sizing: border-box;
}
.wrap{
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
}
.flex{
    display: flex;
} 
.jc-between{
justify-content: space-between;
}
.homepage{display:flex;flex-direction:column;align-items:center;padding:1em 1.5em}

.menu-item{min-width:30%;height:240px;flex:1 1 auto;display:flex;align-items:center;justify-content:center;border:1px solid black;margin:0 7.5px 15px;font-family:'Open Sans Condensed', sans-serif;overflow:hidden;-webkit-transition:all .3s ease;transition:all .3s ease}.menu-item:hover{cursor:pointer}.menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-transition:all 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:all 6s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.menu-item:hover .content{opacity:1}.menu-item.large{height:40vh}.menu-item:first-child{margin-right:7.5px}.menu-item:last-child{margin-left:7.5px}.menu-item .background-image{width:100%;height:100%;background-position:center;background-size:cover}.menu-item .content{text-transform:uppercase;height:90px;padding:0 25px;display:flex;flex-direction:column;align-items:center;justify-content:center;border:1px solid black;background:rgba(255,255,255,0.63);opacity:0.7;position:absolute;-webkit-transition:all .3s ease;transition:all .3s ease;border-radius:2px}.menu-item .content .title{font-weight:bold;margin-bottom:6px;font-size:22px;color:#4a4a4a}.menu-item .content .subtitle{font-weight:lighter;font-size:16px}

.directory-menu{width:100%;display:flex;flex-wrap:wrap;justify-content:space-between}

.checkout-item{width:100%;display:flex;min-height:100px;border-bottom:.1vmax solid darkgrey;padding:.4vmax 0;font-size:1.5vmax;align-items:center;-webkit-transition:all ease-out .5s;transition:all ease-out .5s}.checkout-item .img-containter{width:23%;padding-right:.4vmax}.checkout-item .img-containter img{width:80%;height:auto}.checkout-item .name,.checkout-item .quantity,.checkout-item .price{width:23%}.checkout-item .quantity{padding-left:.5vmax;align-items:center;display:flex}.checkout-item .quantity .arrow{cursor:pointer}.checkout-item .quantity .value{margin:.4vmax;margin-top:.3vmax}.checkout-item:hover{background:#eaeaea}.checkout-item .remove-button{width:100%;height:100%;cursor:pointer;-moz-user-select:none;-ms-user-select:none;-khtml-user-select:none;-webkit-user-select:none;-webkit-touch-callout:none}.checkout-item .remove-button p{padding-left:.6vmax;cursor:pointer;-moz-user-select:none;-ms-user-select:none;-khtml-user-select:none;-webkit-user-select:none;-webkit-touch-callout:none}

.checkout-page{width:55%;min-height:90vh;display:flex;flex-direction:column;align-items:center;margin:2vmax auto 0}@media screen and (max-width: 813px){.checkout-page{width:90vw}}.checkout-page .checkout-header{width:100%;padding:.7vmax 0;display:flex;justify-content:space-between;border-bottom:.1vmax solid darkgrey;font-size:1vmax}@media screen and (max-width: 813px){.checkout-page .checkout-header{font-size:1.5vmax}}.checkout-page .checkout-header .header-block{text-transform:capitalize;width:23%}.checkout-page .checkout-header .header-block:last-child{width:8%}.checkout-page .test-card{text-align:center;margin-top:1vmax;margin-bottom:1vmax;font-size:1.2vmax;color:grey}.checkout-page button{margin-left:auto}.checkout-page .collection-footer .name{font-size:1.5vmax}.checkout-page .total{width:90%;display:flex;flex-direction:row;margin-top:30px;margin-left:auto;font-size:1vmax;justify-content:space-between;color:#333333}.checkout-page .total-text{text-transform:uppercase}

.shop-page{width:79vw;margin:5vmax auto;justify-content:center}@media screen and (max-width: 813px){.shop-page{width:100vw}}.shop-page h1{font-family:'Ubuntu', sans-serif;color:#000000c0}

.collection-preview,.collection-page{display:flex;flex-direction:column;margin:0 auto 1vmax auto;height:-webkit-max-content;height:-moz-max-content;height:max-content;width:-webkit-min-content;width:-moz-min-content;width:min-content}.collection-preview .title,.collection-page .title{font-size:2vmax;margin-bottom:1.5vmax}@media screen and (max-width: 813px){.collection-preview .title,.collection-page .title{font-size:5vmax}}.collection-preview .preview,.collection-page .preview{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr}@media screen and (max-width: 813px){.collection-preview .preview,.collection-page .preview{grid-template-columns:1fr 1fr}}

.custom-button{min-width:1vmax;width:-webkit-max-content;width:-moz-max-content;width:max-content;height:-webkit-max-content;height:-moz-max-content;height:max-content;letter-spacing:.0892857143em;line-height:1em;padding:.6em 1em;font-size:.8em;background-color:black;color:white;text-transform:uppercase;font-family:Roboto, sans-serif;font-weight:700;cursor:pointer;display:flex;justify-content:center;-webkit-transition:.1s all ease-in-out;transition:.1s all ease-in-out;border:1.5px solid black;border-radius:5px}.custom-button:hover{background-color:rgba(255,255,255,0);color:black}.custom-button.google-sign-in{border:1.5px #4285f4 solid;background-color:rgba(255,255,255,0);color:#4285f4}.custom-button.google-sign-in:hover{background-color:#4285f4;color:#fff}.custom-button.inverted{background:rgba(255,255,255,0.4);color:black;font-size:5vmax}.custom-button.inverted:hover{background-color:black;color:white}

.collection-item{display:block;height:-webkit-max-content;height:-moz-max-content;height:max-content;width:12vmax;align-items:center;padding:.5vmax;-webkit-transition:all ease 1s;transition:all ease 1s;position:relative}@media screen and (max-width: 813px){.collection-item{width:40vw;height:-webkit-max-content;height:-moz-max-content;height:max-content;font-size:2vmax}}.collection-item .image{-webkit-transition:all ease .5s;transition:all ease .5s;background:no-repeat center 0;background-size:cover;height:15vmax;margin-bottom:1vh}@media screen and (max-width: 813px){.collection-item .image{height:25vmax}}.collection-item .custom-button{position:absolute;width:80%;opacity:.7;font-size:1em;top:13vmax;left:10%;display:none;-webkit-transition:all ease .4s;transition:all ease .4s}.collection-item .custom-button:hover{opacity:1}@media screen and (max-width: 813px){.collection-item .custom-button{font-size:.7em;position:relative;top:0;left:0;opacity:1;display:block}}.collection-item:hover .custom-button{display:block}.collection-item:hover .image{opacity:.8}.collection-item .collection-footer{width:100%;height:-webkit-min-content;height:-moz-min-content;height:min-content;display:flex;justify-content:space-between;font-size:1em}.collection-item .collection-footer .name{font-family:'Lato', sans-serif;width:90%;margin-bottom:15px}.collection-item .collection-footer .price{font-family:'Lato', sans-serif;font-weight:600;height:1.5vmax}

.collections-overview{display:flex;flex-direction:column}

.collection-page{display:flex;flex-direction:column;width:65vw}@media screen and (max-width: 813px){.collection-page{width:-webkit-max-content;width:-moz-max-content;width:max-content}}.collection-page .title{font-size:1.5vmax;margin:0 auto 1vmax}@media screen and (max-width: 813px){.collection-page .title{font-size:3vmax}}.collection-page .items{display:grid;grid-template-columns:1fr 1fr 1fr 1fr 1fr;grid-gap:0px}@media screen and (max-width: 813px){.collection-page .items{grid-template-columns:1fr 1fr}}.collection-page .items .collection-item{margin-bottom:1vmax}

.group{position:relative;margin:1vmax 0}.group .form-input{background:none;color:rgba(0,0,0,0.836);font-size:1em;padding:.5vmax .5vmax .5vmax .2vmax;display:block;font-weight:bold;width:100%;border:none;border-bottom:1px solid rgba(0,0,0,0.836);margin:25px 0;height:2.5vmax;border-radius:2px}.group .form-input:focus{outline:none}@media screen and (max-width: 812px){.group .form-input{height:5vmax}}.group .form-input:focus ~ .form-input-label{bottom:0;-webkit-transform:translateY(-3vh);transform:translateY(-3vh);font-size:.8em;color:grey}@media screen and (max-width: 812px){.group .form-input:focus ~ .form-input-label{font-size:.6em;-webkit-transform:translateY(-5vh);transform:translateY(-5vh)}}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:rgba(0,0,0,0.836);font-size:1.2em;font-weight:bold;position:absolute;pointer-events:none;left:5px;bottom:.1vmax;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{bottom:0;-webkit-transform:translateY(-3vh);transform:translateY(-3vh);font-size:.8em;color:grey}@media screen and (max-width: 812px){.group .form-input-label.shrink{font-size:.6em;-webkit-transform:translateY(-5vh);transform:translateY(-5vh)}}

.sign-in,.sign-up{width:20vw;display:flex;flex-direction:column;font-family:Roboto, sans-serif}.sign-in .title,.sign-up .title{margin-top:1vmax;font-weight:700}.sign-in span,.sign-up span{font-weight:500;padding-left:1em;font-size:1em;color:#4a4a4a}.sign-in form,.sign-up form{margin-top:1vmax}@media screen and (max-width: 1110px){.sign-in,.sign-up{width:30vw}form{width:30vw}}@media screen and (max-width: 813px){.sign-in,.sign-up{width:90vw}form{width:80vw;margin:auto}}.buttons{display:flex;justify-content:space-between}

@media screen and (max-width: 812px){.sign-up{margin-top:2vmax}}

.loginPage{display:flex;justify-content:space-between;width:70vw;margin:5vh auto;padding:1vmax;border-radius:.5vmax;box-shadow:0 5px 15px rgba(0,0,0,0.41);background-image:linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%)}@media screen and (max-width: 1000px){.loginPage{width:90vw}}@media screen and (max-width: 812px){.loginPage{display:block}}

.cart-icon{width:45px;height:45px;position:relative;display:flex;align-items:center;justify-content:center;cursor:pointer;-webkit-transition:.3s all ease;transition:.3s all ease}.cart-icon .shopping-icon{width:24px;height:24px}.cart-icon .item-count{position:absolute;font-size:10px;font-weight:bold;bottom:12px}

.cart-item{width:100%;display:flex;margin-bottom:.5vmax;border-top:1px solid #000;padding-top:.5vmax}.cart-item img{width:30%}.cart-item:first-of-type{border-top:none}.cart-item .item-details{width:70%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;padding:.3vmax .4vmax}.cart-item .item-details .name{padding-left:2vw;font-size:.8vmax}@media screen and (max-width: 813px){.cart-item .item-details .name{font-size:4vw}}

.cart-dropdown{position:absolute;width:15vmax;height:20vmax;display:flex;flex-direction:column;padding:1vmax;border:1px solid black;background-color:white;top:5vmax;right:5vw;z-index:5;-webkit-transition:.5s all ease;transition:.5s all ease}@media screen and (max-width: 813px){.cart-dropdown{top:16.2vw;width:100vw;height:92vh;position:fixed;left:0}}.cart-dropdown .cart-items{display:flex;flex-direction:column;overflow-y:scroll;margin-bottom:.8vmax}@media screen and (max-width: 813px){.cart-dropdown .cart-items{height:auto;font-size:2vmax}}.cart-dropdown .empty-message{font-family:'Lato', sans-serif;font-weight:300;margin:auto;font-size:1vmax}@media screen and (max-width: 813px){.cart-dropdown .empty-message{font-size:2vmax}}.cart-dropdown button{height:-webkit-max-content;height:-moz-max-content;height:max-content;margin-top:auto;margin-bottom:1vh}

header{background:rgba(255,255,255,0.884);height:5vmax;display:flex;align-items:center;padding-left:10vw;padding-right:10vw;position:fixed;top:0;width:100vw;z-index:10;box-shadow:0 0 15px #00000094;justify-content:space-between}@media screen and (max-width: 813px){header{height:16vw}}header .logo{font-family:'Roboto', sans-serif;text-decoration:none;color:#444444;font-size:3vmax}header .option{cursor:pointer}header .title{display:flex}header .title p{color:#333;font-family:'Ubuntu', sans-serif;font-weight:500;font-size:.9vmax;text-transform:uppercase;margin-top:auto;margin-bottom:.1vmax}header .navigation-menu ul{display:flex;list-style-type:none}header ul{display:flex}@media screen and (max-width: 568px){header ul{padding-left:20px}}header li{font-size:1.2rem;margin-left:1em;font-weight:300;align-items:center;display:flex;width:-webkit-max-content;width:max-content;width:-moz-max-content}@media screen and (max-width: 813px){header li{font-size:1em}header li:first-of-type{margin:0}header li:last-of-type{margin-right:1em}}header li a{width:-webkit-max-content;width:max-content;width:-moz-max-content;text-decoration:none;color:#333;font-weight:400}header li a:hover{color:#000}#header-space{min-height:5vmax;visibility:hidden}@media screen and (max-width: 813px){#header-space{min-height:8vmax}}

